<template>
	<div id="attributs-urbains" class="view-scroll">
		<h1>Les attributs urbains cibls</h1>
	<!-- Partie 1 -->
		<div class="center-col">
			<h3>Quels attributs du paysage urbain ont fait lobjet de proccupations et de valorisations par les mdias montralais{{'\xa0'}}?</h3>
			<p>Les attributs urbains qui ont retenu lattention des mdias se rsument aux lments suivants{{'\xa0'}}: le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Commerce']}">commerce</span>, le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Parc']}">parc</span>, la <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Rue']}">rue</span>  et la <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Ville']}">ville</span>. Tandis que l<span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Automobile']}">automobile</span>, le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Corridor sanitaire']}">corridor sanitaire</span> et le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Piton']}">piton</span> ont occup des intrts secondaires dans les articles de presse.</p>
		</div>
		<div id="attributs-sommaire">
			<BubbleCluster
				:chartID="'bubbles-sommaire'"
				:dataArray="attributsSommaireColored"
				:weightKey="'occurrences'"
				:wordKey="'attributs'"
				:colorKey="'color'"
			/>
		</div>
		<div class="center-col">
			<p>Il est intressant de noter que les trois dernires semaines de confinement sont celles o les occurrences pour chacun des attributs urbains sont les plus fortes, en lien avec le dconfinement et le retour du beau temps (printemps).</p>

			<!-- <p>La <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Rue']}">rue</span> est la principale proccupation de lespace public montralais. Ensuite, le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Parc']}">parc</span> constitue lattribut majeur du paysage urbain. L<span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Automobile']}">automobile</span> et le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Commerce']}">commerce</span> font lobjet dattention auprs des mdias en regard de lactivit conomique et de la mobilit. Par leur occurrence, la <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Rue']}">rue</span> et le <span class="attribute-highlight" :style="{ backgroundColor: this.attrColors['Parc']}">parc</span> sont les figures dominantes et valorises du paysage urbain montralais en mode confinement. En regard de lensemble des attributs rpertoris dans cette analyse, elles confirment leur importance pour la sant et le bien-tre de la population urbaine  limage de la ville hyginiste du 19e sicle.</p> -->
		</div>

	<!-- Partie 2 -->
		<!-- <div class="center-col">
			<h3>Combien d'articles de presse ont abords chacun des principaux attributs du paysage urbain{{'\xa0'}}?</h3>
		</div> -->
		<div id="semaines-wrap">
			<div class="attributs-semaines" v-for="(attribut,i) in attrList" :key="attribut+i">
				<h4><span class="attribute-highlight" :style="{ backgroundColor: attrColors[attribut]}">{{ attribut+'\xa0'+':' }}</span></h4>
				<div class="flex-timeline">
					<Timeline
						:chartID="'timeline-attributs-semaine'"
						:dataArray="attributsSommaireSemaine[attribut]"
						:wordKey="'date'"
						:scaleLabel="'Semaine'"
						:showScaleLabel="i == attrList.length-1"
						:showScaleLine="false"
						:timeKey="'date'"
						:showDates="false"
						:startDate="toDate('03/13/20','/')"
						:endDate="toDate('05/16/20','/')"
						:colorKey="'color'"
						:marksPos="'intra'"
						:weightKey="'size'"
						:idKey="'n'"
					/>
				</div>
			</div>
		</div>

		<!-- Partie 3 -->
		<div class="center-col">
			<h3>Quelles sont les proccupations et valorisations relayes par les mdias au sujet des divers attributs urbains recenss{{'\xa0'}}?</h3>
			<p>Les diffrents attributs urbains montralais sont dcrits dans les mdias par des qualificatifs diversifis qui ont t rpertoris dans cette veille informationnelle sous forme de nuages de mots. Les proccupations et valorisations des qualificatifs des attributs sont prsentes dans la figure ci-contre selon loccurrence et selon trois groupes distinctifs qui les caractrisent{{'\xa0'}}:
				<ul>
					<li><div class="legende" :style="{backgroundColor: groupesColors.problemes.color}"></div> Problmes{{'\xa0'}};</li>
					<li><div class="legende" :style="{backgroundColor: groupesColors.solutions.color}"></div> Solutions <i>(actions)</i>{{'\xa0'}};</li>
					<li><div class="legende" :style="{backgroundColor: groupesColors.fonctions.color}"></div> Fonctions <i>(usages/sujets/objet)</i>.</li>
				</ul>
			</p>
		</div>

		<div id="cluster-external-container">
			<div id="attributs-pane">
				<h4>Attributs</h4>
				<ul>
					<li v-for="(code, i) in filteredCodesList" v-bind:key='i+"_li"'>
						<input :key='i+"_input"' type="radio" :disabled="!code.hasBubbles" :id='code.code+"_radio"' :value='code.code' v-model="choix">
						<label :key='i+"_label"' :for='code.code+"_radio"' :class="{'disabled': !code.hasBubbles}">{{ code.code }}</label>
					</li>
				</ul>
			</div>
			<div id="visualisation-pane-outer">
				<div id="visualisation-pane-inner">
					<div :class="{'noquotes': attributSingleCaracterisationTimeline == (undefined && null && '')}" id="quotes-pane">
						<h4>Extraits</h4>
						<transition-group name="quotes" mode="out-in" tag="div">
							<div class="quote-block" v-for="(quote,i) in attributSingleCaracterisationTimeline" :key="i+quote.date+quote.mot+'-block'" :style="{borderColor: quote.color}">
								<!-- <div class="legende"></div> -->
								<h5 :key="i+quote.date+quote.mot" :style="{color: quote.color}">{{ quote.id }}&nbsp;: {{ quote.mot }}</h5>
								<p v-for="(q,index) in quote.citation" :key="index+quote.date+quote.mot+'-quote'">&nbsp;{{ q }}&nbsp;</p>
							</div>
						</transition-group>
					</div>
					<div id="bubbles-pane">
						<h4>Occurrences des qualificatifs</h4>
						<div id="bubbles-pane-inner">
							<BubbleCluster
								:chartID="'bubbles-caracterisation'"
								:dataArray="attributSingleCaracterisation"
								:weightKey="'occurrences'"
								:wordKey="'mot'"
								:colorKey="'color'"
								:textColorKey="'textColor'"
								:borderColorKey="'borderColor'"
							/>
						</div>
					</div>
				</div>
				<div id="timeline-pane">
					<h4>Parutions</h4>
					<div id="timeline-pane-inner">
						<Timeline
							:chartID="'timeline-caracterisation'"
							:dataArray="attributSingleCaracterisationTimeline"
							:wordKey="'mot'"
							:textKey="'citation'"
							:idKey="'id'"
							:timeKey="'date'"
							:showDates="true"
							:startDate="toDate('03/13/20','/')"
							:endDate="toDate('05/16/20','/')"
							:colorKey="'color'"
							:showScaleLine="true"
							:showScaleLabel="true"
							:scaleLabel="'Semaine'"
							:marksPos="'inter'"
						/>
					</div>
				</div>
			</div>
		</div>
		<div class="center-col">
			<ChapterNav :previous="true" :next="true" />
		</div>
	</div>
</template>

<script>
import AttributsDetail from '@/assets/data/attributs-detail.json'
import AttributsSommaire from '@/assets/data/attributs-sommaire.json'
import BubbleCluster from '@/components/BubbleCluster'
import Timeline from '@/components/Timeline'
import ChapterNav from '@/components/ChapterNav'

export default {
	name: 'AttributsUrbains',
	components: {
		BubbleCluster,
		Timeline,
		ChapterNav
	},
	data() {
		Object.freeze(AttributsSommaire)
		Object.freeze(AttributsDetail)
		return {
			choix: 'Autobus',
			AttributsSommaire,
			AttributsDetail,
			expandedQuotations: [],
			attrList: ["Automobile","Commerce","Corridor sanitaire","Parc","Piton","Rue","Ville"]
		}
	},
	created() {
		this.expandedQuotations = [];
		AttributsDetail.Quotations.forEach(quotation => {
			this.expandedQuotations.push({
				'Quotation Name': quotation['Quotation Name'],
				'Document': quotation['Document'],
				'Quotation Content': quotation['Quotation Content'],
				'Citation': quotation['Citation'] == (null && undefined && '') ? null : quotation['Citation'],
				'Date': quotation['Date'] == (null && undefined && '') ? null : quotation['Date'],
				'Codes': quotation['Codes'].split(/ *; */),
				'Reference': quotation['Reference'],
				'Groupe': quotation['Groupe']
			})
		})
		Object.freeze(this.expandedQuotations);
	},
	computed: {
		codesList() {
			var flags = [], codes = [];
			for ( let i = 0; i < this.expandedQuotations.length; i++ ) {
				var quotationCodes = this.expandedQuotations[i]['Codes'];
				for ( let j = 0; j < quotationCodes.length; j++ ) {
					if( flags[quotationCodes[j]] ) continue;
					flags[quotationCodes[j]] = true;
					codes.push(quotationCodes[j]);
				}
			}
			return codes.sort(Intl.Collator().compare)
		},

		attrColors() {
			var colors = {};
			this.attrList.forEach((attr,i) => {
				colors[attr] = 'hsl('+i / this.attrList.length * 360+', 60%, 70%)'
			})
			return colors
		},

		groupesColors() {
			var prob = {r:80, g:105, b: 75};
			var sol = {r:120, g:175, b: 99};
			var fonc = {r:195, g:225, b: 135};
			var groupes = {problemes: prob, solutions: sol, fonctions: fonc};
			['problemes','solutions','fonctions'].forEach(groupe => {
				groupes[groupe].color = 'rgb('+groupes[groupe].r+','+groupes[groupe].g+','+groupes[groupe].b+')'
			})
			console.log(groupes)
			return groupes
		},

		attributsCaracterisations() {
			const regExFilter = /(^[,'\s]+(\s*.[,'\s]+)*)|(([,'\s]+.\s*)*[,'\s]+$)/g // Ancienne version: /(^[,'\s]+)|([,'\s]+$)/g
			//const regExFilter = /(^[,'\s]+)|([,'\s]+$)/g
			var occurrences = {};
			this.codesList.forEach(code => {
				var currentCaracterisations = []
				const correspondingQuotations = this.expandedQuotations.filter( quotation => quotation['Codes'].some( quotationCodes => quotationCodes === code ));
				occurrences[code] = [];
				var caracterisation = {};
				caracterisation[code] = [];
				correspondingQuotations.forEach(singleQuotation => {
					caracterisation[code].push( singleQuotation['Quotation Content'].replace(regExFilter, '').toLowerCase() )
				})
				const map =  caracterisation[code].reduce((acc, e) => acc.set(e, (acc.get(e) || 0) + 1), new Map());
				map.forEach( (value, key) => {
					const groupesArray = correspondingQuotations.filter(q => q['Quotation Content'].replace(regExFilter, '').toLowerCase() == key).map(qFiltered => qFiltered['Groupe'].trim());
					let palette = this.defineColor(groupesArray);
					currentCaracterisations.push({
						mot: key,
						occurrences: value,
						groupe: groupesArray,
						color: palette.color,
						textColor: palette.textColor,
						borderColor: palette.borderColor
					});
				})
				occurrences[code] = currentCaracterisations.filter(caracterisation => caracterisation['occurrences'] > 1).sort((a,b) => b['occurrences'] - a['occurrences']).slice(0,12);
			});
			return occurrences
		},

		attributSingleCaracterisation() {
			return Object.freeze(this.attributsCaracterisations[this.choix])
		},

		attributsCaracterisationsTimeline() {
			const haveDate = this.expandedQuotations.filter( quotation => quotation['Date'] != (undefined && null && '') && quotation['Date'].trim() != (undefined && null && ''));
			const haveCitation = haveDate.filter( quotation => quotation['Citation'] != (undefined && null && ''))
			var datedCaract = {};
			this.codesList.forEach(code => {
				datedCaract[code] = [];
				const haveCode = haveCitation.filter( quotation => quotation['Codes'].some( quotationCodes => quotationCodes === code ));
				if (haveCode.length > 0) {
					haveCode.forEach( codedQuotation => {
						let palette = this.defineColor([codedQuotation['Groupe']]);
						datedCaract[code].push({
							mot: codedQuotation['Quotation Name'],
							date: this.toDate(codedQuotation['Date'],'/'), // Ici: trouver un moyen de contenir l'info que plusieurs citations mme date pour modifier affichage dans timeline
							citation: [codedQuotation['Citation']], // Ici: grer lorsque plusieurs citations se rapportent  la mme date + mme mot (probablement ensuite utiliser if (quotation.citation.length>1...))
							groupe: codedQuotation['Groupe'],
							color: palette.color,
							textColor: palette.textColor,
							borderColor: palette.borderColor
						})
					})
					datedCaract[code].sort((a,b) => a.date - b.date).forEach((cQ,i) => cQ.id = String.fromCharCode(('A').charCodeAt(0)+i))
				} else {
					datedCaract[code] = null;
				}
			});
			return datedCaract
		},

		attributSingleCaracterisationTimeline() {
			return Object.freeze(this.attributsCaracterisationsTimeline[this.choix])
		},

		attributsSommaireSemaine() {
			var sommaire = {}
			this.attrList.forEach(attribut => {
				sommaire[attribut] = []
				this.AttributsSommaire['semaines'].forEach(semaine => {
					sommaire[attribut].push({
						date: this.toDate(semaine['Date milieu'].concat('/12'),'/'),
						n: semaine[attribut],
						size: Math.sqrt(semaine[attribut]*200),
						color: this.attrColors[attribut]
					})
				})
			})
			return Object.freeze(sommaire)
		},

		attributsSommaireColored() {
			var coloredData = []
			this.AttributsSommaire['sommaire'].filter(attr => attr['occurrences']>10).forEach(attr => {
				coloredData.push({
					attributs: attr.attributs.trim(),
					occurrences: attr.occurrences,
					color: this.attrColors[attr.attributs.trim()] != (undefined && null && '')? this.attrColors[attr.attributs.trim()] : null
				})
			})
			return coloredData
		},

		filteredCodesList() {
			var filteredCodes = []
			this.codesList.forEach(thisCode => {
				filteredCodes.push({
					code: thisCode,
					hasBubbles: this.attributsCaracterisations[thisCode].length>0 ? true : false
				})
			})
			return filteredCodes
		}		
	},	
	methods: {
		toDate(dateString, separator) {
			const dateStr = dateString.split(separator);
			const date = new Date(20+dateStr[2].trim(), parseInt(dateStr[0].trim(), 10)-1, dateStr[1].trim(), dateStr.length>3 ? dateStr[3].trim():"" );
			return date
		},
		defineColor(array) {
			var r,g,b;
			var colSum = {r:0 ,g:0 ,b:0}
			const colKeys = ['r','g','b']
			const gr = this.groupesColors;
			var problemes = {n:0, r: gr.problemes.r, g: gr.problemes.g, b: gr.problemes.b};
			var solutions = {n:0, r: gr.solutions.r, g: gr.solutions.g, b: gr.solutions.b};
			var fonctions = {n:0, r: gr.fonctions.r, g: gr.fonctions.g, b: gr.fonctions.b};
			const total = array.length;
			
			array.forEach(item => {
				switch(item) {
					case "Problme":
						++problemes.n;
						colKeys.forEach(primary => { colSum[primary] += Math.pow(problemes[primary],2) });
						break;
					case "Fonction":
						++fonctions.n;
						colKeys.forEach(primary => { colSum[primary] += Math.pow(fonctions[primary],2) });
						break;
					case "Solution":
						++solutions.n;
						colKeys.forEach(primary => { colSum[primary] += Math.pow(solutions[primary],2) });
						break;
					default:
						console.log('oops, aucun groupe ne semble tre correctement dfini pour :'+item);
				}
			})

			r = Math.sqrt(colSum.r/total);
			g = Math.sqrt(colSum.g/total);
			b = Math.sqrt(colSum.b/total);
			let theColor = 'rgb('+r+','+g+','+b+')';

			var theTextColor = 'rgb(24,24,24)';
			var theBorderColor = null //'rgb(24,24,24)';
			if ((.2126*r + .7152*g + .0722*b)<165) { // Threshold de pseudo-luminance pour inverser couleur de typo (en ralit il faudrait transposer gamma-RGB(0-255) vers linaire)
				theTextColor = 'rgb(255,255,255)'
				theBorderColor = null
			}
			return {color: theColor, textColor: theTextColor, borderColor: theBorderColor}
		},
	}
}
</script>

<style scoped>
#attributs-urbains {
	box-sizing: border-box;
	width: 100%;
	height: 100%;
}
#attributs-sommaire {
	margin: -100px 0px -50px 0px;
	width: 100%;
	height: 75vh;
}
.attribute-highlight {
	padding: 0px 4px;
	margin: 0px 2px;
	border-radius: 3px;
	font-weight: 500;
	color: white;
	text-shadow: 1px 1px 2px rgba(0,0,0,.25)
}

#semaines-wrap {
	box-sizing: content-box;
	width: 850px;
	padding: 35px 150px 50px 0px;
	margin: 0px auto 50px auto;
}
.attributs-semaines {
	display: flex;
	height: auto;
	max-width: 1200px;
	width: 100%;
	margin: 0px auto;
	align-items: center;
}
.attributs-semaines h4 {
	text-align: right;
	user-select: none;
	font-family: 'Poppins', sans-serif;
	font-weight: 500;
	font-size: 15px;
	width: 152px;
	height: 1em;
	margin: 0px;
}
.flex-timeline {
	flex: 1
}

.legende {
	position: relative;
	display: inline-block;
	width: 22px;
	height: 22px;
	border-radius: 50%;
	padding: 0px;
	margin: 0px 10px;
	bottom: -5px;
}

#cluster-external-container {
	display: flex;
	flex: 1;
	position: relative;
	height: 90%;
	margin: 90px;
	right: 5px;
	overflow: hidden;
	border-radius: 12px;
	box-shadow: 2px 12px 25px -10px rgba(0,0,0,.5);
}

#cluster-external-container h4 {
	user-select: none;
	font-family: 'Poppins', sans-serif;
	font-weight: 600;
	font-size: 12px;
	letter-spacing: 3px;
	line-height: 1em;
	margin: 10px;
	padding: 10px;
	color: rgb(175, 175, 175);
	text-align: center;
	text-transform: uppercase;
	border-bottom: 1px solid rgba(175, 175, 175, 0.3);
}

#attributs-pane {
	display: inline-flex;
	flex-direction: column;
	position: relative;
	top: 0px;
	left: 0px;
	height: 100%;
	width: 250px;
	/* flex: 0 0 250px; quivalent de width 250px mais en prcisant flex-grow 0 et flex-shrink 0 */
	background: rgb(48, 48, 48);
	overflow: hidden;
}
#attributs-pane ul {
	flex: 1;
	overflow-y: auto;
	overflow-x: hidden;
	padding: 0px 0px 20px 0px;
	text-indent: 0px;
	margin: 0px;
}
#attributs-pane li {
	display: block;
	list-style-type: none;
	text-indent: 0;
	margin: 0px;
