}
#timeline-pane-inner {
	margin: 15px 25px
}
#visualisation-pane-inner {
	text-align: left;
	display: inline-flex;
	flex-direction: row;
	position: relative;
	top: 0px;
	left: 0px;
	height: 100%;
	flex: 1;
	background: rgb(255, 255, 255);
	overflow: hidden;

}
#quotes-pane {
	display: inline-block;
	position: relative;
	top: 0px;
	left: 0px;
	width: 275px;
	background-color: rgb(241, 242, 245);
	height: 100%;
	overflow-y: auto;
	overflow-x: hidden;
